.app {
	display: grid;
	padding: 30px;
	grid-template-columns: 2fr 1fr;
	grid-template-rows: 1fr;
	justify-content: center;
	align-items: center;
}

.game {
	width: 560px;
	height: 560px;
	display: flex;
	flex-wrap: wrap;
}

.game img {
	width: 70px;
	height: 70px;
}

.score-board {
	text-align: center;
	padding-right: 10rem;
	color: red;
	font-family: Georgia, "Times New Roman", Times, serif;
}

.score-board h1:first-child {
	font-size: 3rem;
}

.score-board h1:nth-child(2) {
	color: gray;
}
